body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666666;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.slide-container {
  background: black;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  /* padding: 1rem; */
}

.no-image {
  font-size: 30px;
  color: white;
  min-height: 200px;
  height: 100%;
}

.text-box {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.home-title {
  font-size: 3rem;
  /* margin-bottom: 10px; */
}

.center-text {
  text-align: center;
}

.container {
  padding-top: 4rem;
  padding-bottom: 1rem;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.about-section {
  margin-top: 20px;
  text-align: center;
}

.about-text {
  padding: 2rem;
  font-size: 1.5em;
}

.tech {
  padding-left: 4rem;
  padding-right: 4rem;
}

.project-container {
  padding-bottom: 1rem;
}

.projects-text {
  color: #666666;
}

.company-name {
  padding-bottom: 1rem;
  padding-top: 1rem;
}

.section-text {
  text-align: left;
}

.light-text {
  color: white;
}

.prev, .next {
  cursor: pointer;
  position: fixed;
  top: 50%;
  width: auto;
  padding: 15px;
  margin-top: -22px;
  color: #999999;
  font-weight: bold;
  font-size: 33px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev:hover, .next:hover {
  color: rgba(0,0,0,0.8);
}

@media only screen and (max-width: 500px) {
  .projects-box {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
